import React from "react";
import PropTypes from "prop-types";

import st from "./style";
import IconPlaceHolderUser from '../../icons/IconPlaceHolderUser';

import CircleWave from '../../images/CircleWave';

class UserCard extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
      
    };
  }

  render() {
    return (
      <div style={{...st.container,...this.props.style}}>
        <div style={st.ctnImage}>
          <CircleWave />
          {this.props.userImage?
            <img style={st.image} src={this.props.userImage}/>
            :<IconPlaceHolderUser style={st.image}/>
          }
          
        </div>
        <p style={st.label}>
          {this.props.userName}
        </p>
        <div style={st.qnt}>
        { this.props.qntListings?
          <span style={st.qntListings}>
            {this.props.qntListings} 
            {parseInt(this.props.qntListings)>1?' produtos':' produto'}
          </span>
          :null
        }

        {
          this.props.qntTransactions?
          <span style={st.qntTransactions}> 
            {' / '}
            {this.props.qntTransactions} 
            {parseInt(this.props.qntTransactions)> 1?' pedidos':' pedido'}
          </span>
          :null
        }  
          
        </div>
      </div>
    );
  }
}

UserCard.propTypes = {
  onClick: PropTypes.func,
  userName: PropTypes.string,
  userImage: PropTypes.string,
  qntListings: PropTypes.string,
  qntTransactions: PropTypes.string,
  style: PropTypes.object,
};
UserCard.defaultProps = {
  onClick: () => {},
  userName: null,
  userImage: null,
  qntListings: null,
  qntTransactions: null,
  style: {},
};

export default UserCard;
