import React from "react";
import PropTypes from "prop-types";

import st from "./style";
import IconPlaceHolderCover from '../../icons/IconPlaceHolderCover';
import UserCard from '../UserCard';

class UserCoverCard extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
      
    };
  }

  render() {

    const PlaceHolder = () => (
      <div style={st.ctnPlaceHolder}>
        <IconPlaceHolderCover style={st.iconPlaceHolder}/>
      </div>
    )

    return (
      <div style={{...st.container(this.props),...this.props.style}}>
        {
          this.props.cover?
          <img style={st.cover} src={this.props.cover}/>
          :<PlaceHolder style={st.iconPlaceHolder}/>
        }
        
        <div style={st.ctnUser}>
          <UserCard
            userName={this.props.userName}
            userImage={this.props.userImage}
            qntListings={this.props.qntListings}
            qntTransactions={this.props.qntTransactions}
          />
        </div>
      </div>
    );
  }
}

UserCoverCard.propTypes = {
  onClick: PropTypes.func,
  userName: PropTypes.string,
  userImage: PropTypes.string,
  cover: PropTypes.string,
  qntListings: PropTypes.string,
  qntTransactions: PropTypes.string,
  boxShadow: PropTypes.bool,
  borderBottom: PropTypes.bool,
  paddingBottom: PropTypes.number,
  backgroundColor: PropTypes.string,
  style: PropTypes.object,
};
UserCoverCard.defaultProps = {
  qntListings: null,
  qntTransactions: null,
  boxShadow:false,
  borderBottom: false,
  paddingBottom: 0,
  backgroundColor: null,
  onClick: () => {},
  style: {},
};

export default UserCoverCard;
